import axios from 'axios';
global.Buffer = global.Buffer || require('buffer').Buffer

// TODO: Remove console log after POC, the logs are for benchmarking / debug
/**
 * API class related to Dolby API
 */
export default class DolbyAPI {
    static async analyzeAudio(apiKey, filename) {
        const config = {
            method: 'post',
            url: 'https://api.dolby.com/media/analyze',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
                'x-api-key': apiKey,
            },
            data: {
                input: `dlb://in/${filename}`,
                output: 'dlb://out/out.json',
            },
        }
        const response = await axios(config);

        await DolbyAPI._waitUntil('https://api.dolby.com/media/analyze', response.data.job_id, apiKey);

        const downloadResponse = await DolbyAPI._downloadOutput('dlb://out/out.json', apiKey);

        return downloadResponse;
    }

    static async diagnoseAudio(apiKey, filename) {
        const config = {
            method: 'post',
            url: 'https://api.dolby.com/media/diagnose',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
                'x-api-key': apiKey,
            },
            data: {
                input: `dlb://in/${filename}`,
            },
        }
        const response = await axios(config);

        const result = await DolbyAPI._waitUntil('https://api.dolby.com/media/diagnose', response.data.job_id, apiKey);

        return result;
    }

    static async _uploadToDolby(file, size, filename, apiKey) {
        const config = {
            method: 'post',
            url: 'https://api.dolby.com/media/input',
            headers: {
                'x-api-key': apiKey,
                'Content-Type': 'application/json',
                'Accept': 'application/json',
            },
            data: {
                url: `dlb://in/${filename}`,
            },
        };
        const response = await axios(config);
        const uploadConfig = {
            method: 'put',
            url: response.data.url,
            data: file,
            maxContentLength: Infinity,
            maxBodyLength: Infinity,
            headers: {
                'Content-Type': 'application/octet-stream',
                'Content-Length': size,
            },
        };

        return await axios(uploadConfig);
    }

    static async _checkJobStatus(url, jobId, apiKey) {
        const config = {
            method: 'get',
            url,
            headers: {
                'x-api-key': apiKey,
                'Content-Type': 'application/json',
                Accept: 'application/json',
            },
            params: {
                'job_id': jobId,
            },
        };

        return await axios(config);
    }

    static async _waitUntil(url, jobId, apiKey) {
        return await new Promise((resolve) => {
            const interval = setInterval(async () => {
                const statusResponse = await DolbyAPI._checkJobStatus(url, jobId, apiKey);

                console.log('checking process status', statusResponse?.data);

                if (statusResponse.data.status !== 'Running' && statusResponse.data.status !== 'Pending') {
                    clearInterval(interval);
                    resolve(statusResponse.data);
                }
            }, 1000);
        });
    }

    static async _downloadOutput(url, apiKey) {
        const config = {
            method: 'get',
            url: 'https://api.dolby.com/media/output',
            headers: {
                'x-api-key': apiKey,
                'Accept': 'application/octet-stream',
            },
            responseType: 'stream',
            params: {
                url,
            },
        };

        const response = await axios(config);

        return new Promise((resolve, reject) => {
            return resolve(response.request.responseURL);
        });
    }
}
