import './App.css';
import { useState } from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import DolbyAPI from './utils/DolbyAPI';

function App() {
  const [diagnosisResult, setDiagnosisResult] = useState(null);
  const [apiKey, setApiKey] = useState(null);
  const [audioInput, setAudioInput] = useState(null);
  const downloadOutput = (downloadUrl, filename) => {
    const link = document.createElement('a');
    link.href = downloadUrl;
    link.setAttribute(
      'download',
      filename,
    );

    // Append to html link element page
    document.body.appendChild(link);

    // Start download
    link.click();

    // Clean up and remove the link
    link.parentNode.removeChild(link);
  }

  const handleAnalyzeAudio = async () => {
    await DolbyAPI._uploadToDolby(audioInput, audioInput.size, audioInput.name, apiKey);

    const analysisResultUrl = await DolbyAPI.analyzeAudio(apiKey, audioInput.name);

    downloadOutput(analysisResultUrl, `analysis.json`);
  }

  const handleDiagnoseAudio = async () => {
    const result = await DolbyAPI.diagnoseAudio(apiKey, audioInput.name);

    console.log('diagnosis result', result);
  }

  return (
    <div className="container">
      <h1>Dolby Analysis API</h1>
      <Form>
        <Form.Group className="mb-3 d-flex justify-content-between">
          <Form.Label>API Keys</Form.Label>
          <Form.Control
            name="target_level"
            type="text"
            onChange={(e) => setApiKey(e.target.value)}
          />
        </Form.Group>

        <Form.Group controlId="formFile" className="mb-3">
          <Form.Label>
            <h4>Upload an audio file to test out the API</h4>
          </Form.Label>
          <Form.Control name="inputAudio" type="file" accept="audio/*" onChange={(e) => { setAudioInput(e.target.files[0]) }}/>
        </Form.Group>

        <Button className="mb-3 ml-3" onClick={handleAnalyzeAudio}>Analyze audio</Button> <br />
        {/* <Button className="mb-3 ml-3" onClick={handleDiagnoseAudio}>Diagnose audio</Button> */}
      </Form>

      {diagnosisResult && (<div className="d-flex">
        <pre className="mr-3">
          Input: <br />
          {JSON.stringify(diagnosisResult.before, null, 2)}
        </pre>
        <pre>
          Output: <br />
          {JSON.stringify(diagnosisResult.after, null, 2)}
        </pre>
      </div>)}
    </div>
  );
}

export default App;
